body{
  height: 100vh;
  background: rgb(18,140,126);
  background: linear-gradient(to top,#87b597,#2bf0c8);
}
.container{
  width: 90vw;
  margin: auto;
}
.number-input{
  height: 70px;
  width: 90vw;
  margin: 50px auto 0 auto;
  text-align: center;
  font-size: 50px;
}
.msg-input{
  height: 170px;
  width: 90vw;
  margin: 10px auto 0 auto;
  font-size: 25px;  
}
.logo{
  height: 100px;
  width: 100px;
  background: url('https://static.whatsapp.net/rsrc.php/v3/yP/r/rYZqPCBaG70.png');
  background-size: 100px 100px;
  margin: auto;
}
input::placeholder{
  color: #bbb
}
input{
  color: #111;
  text-shadow: 2px 2px 7px #fff;
}
.f-30{
  font-size: 30px!important;
}
.f-10{
  font-size: 20px;
  text-align: center;
  margin: 20px 0 0 0;
  color: green;
}

.send-btn{
  height: 70px;
  width: 90vw;
  margin: 50px auto !important;
  font-size: 40px !important;
}